// }

.modal__button {
  margin: 24px auto 0;
  width: 114px;
  height: 42px;
  max-width: 100%;

  background: var(--brand-color);
  border-radius: 10px;
  border: 0;
}

.modal__button-text {
  font-family: var(--main-font-family);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--main-text-color);
}

.modal__map {
  max-width: 224px;
  max-height: 199px;

  @media screen and (min-width: 480.1px) {
    max-width: 448px;
    max-height: 260px;
  }
}
